import react from 'react';
import './App.scss';
import {Container,Box,Grid,Button} from '@mui/material/';

import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';


import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';



import Carousel from 'react-bootstrap/Carousel';

import clifton from "./certificates/clifton.jpg";
import javaoop from "./certificates/javaoop.jpg";
import javaprog from "./certificates/javaprog.jpg";
import mtsateamplayer from "./certificates/mtsateamplayer.jpg";
import responsiveweb from "./certificates/responsiveWeb.jpg";
import sqlcert from "./certificates/sql.jpg";
import webdevtesda from "./certificates/webdevtesda.jpg";
import webdevtraining from "./certificates/webdevtraining.jpg";


import designthinkingsap from "./certificates/designthinking.png";
import screenprog from "./certificates/screen.PNG";

import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();


const certificates = [
    {
        name:"Design Thinking",
        images:designthinkingsap,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"Screen Programming",
        images:screenprog,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"Responsive Web",
        images:responsiveweb,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"SQL Course",
        images:sqlcert,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"Java OOP",
        images:javaoop,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"Java Programming",
        images:javaprog,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"MTSA Teamplayer",
        images:mtsateamplayer,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"TESDA Web Development Training",
        images:webdevtraining,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"TESDA Web Development Qualification",
        images:webdevtesda,
        href:"",
        desc:"",
        category:""
    },
    {
        name:"Clifton Strengths",
        images:clifton,
        href:"",
        desc:"",
        category:""
    }

    
  
]
export default function Certificate(){
    return(
        <Box className="certificate-con" container id="certificate">
            <Box className="certificate-text-con" item>
                <Typography variant="h2" align="center">Certificates</Typography>
                <Divider/>
                <Typography variant="body1" align="center">
                    List of certificates from different websites, company, and trainings
                </Typography>
                <Card>
                    <CardMedia
                    component=""
                    image=""
                    alt=""
                    />
                </Card>
            </Box>
            <Box className="certificate-list-con" item >
                    <Carousel className="carousel-con h-100" >
                    {
                        certificates.map(certificate=>{
                            return(
                            <Carousel.Item interval={3000} className="carousel-item" >
                                <Typography variant="h6">{certificate.name}</Typography>
                                <figure className="fig" >
                                    <img
                                    // className="d-block w-100 h-100"
                                    src={certificate.images}    
                                    className="cert-images"
                                    />
                                </figure>
                               
                                {/* <Carousel.Caption>
                                <h3>First slide label</h3>
                                <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                                </Carousel.Caption> */}
                            </Carousel.Item>
                            
                            )
                        })
                    }
                    
                    </Carousel>
                    
            </Box>
        </Box>
    )
}
