export default function Projects(){
    return(
        <Box className="projects-con" id="projects">
            <Typography variant="h2" align="center">Projects | Simple Task</Typography>
            {/* <Divider/> */}
            <Container className="projects-con-list">
                {
                    projects.map(project=>{
                        return(
                            <Card sx={{ maxWidth: 385 }} className="projects-card"  data-aos="zoom-in">
                                <CardMedia
                                    component="img"
                                    image={project.images}
                                    alt="techskill image gif"
                                    className="project-img"
                                />
                                <CardContent>
                                    <Typography gutterbottom variant="h5" component="div" color="secondary">
                                        {project.name}
                                    </Typography>
                                    <Typography variant="body2">
                                        {project.desc}
                                    </Typography>
                                </CardContent>
                                <Divider/>
                                <CardContent>
                                    <Typography variant="body2">
                                        Category: {project.category}
                                    </Typography>
                                </CardContent>
                                <Divider/>
                                <CardActions>
                                    <Button size="small" href={project.href} target="_blank">Open</Button>
                                </CardActions>
                            </Card>
                        )
                    })
                }
            </Container>
            {/* <Divider/> */}
            {/* <Typography variant="h2" align="center">Programs</Typography>
            <Container className="projects-con-list2">
                {
                    githublinks.map(githublink=>{
                        return(
                            <Card sx={{ maxWidth: 385 }} className="projects-card"  data-aos="zoom-in">
                               
                                <CardContent>
                                    <Typography gutterbottom variant="h6" component="div" color="secondary">
                                        {githublink.name}
                                    </Typography>
                                    <Typography variant="body2">
                                        {githublink.desc}
                                    </Typography>
                                </CardContent>
                                <Divider/>
                                <CardContent>
                                    <Typography variant="body2">
                                        Category: {githublink.category}
                                    </Typography>
                                </CardContent>
                                <Divider/>
                                <CardActions>
                                    <Button size="snall" href={githublink.href} target="_blank">Github Link</Button>
                                </CardActions>
                            </Card>
                        )
                    })
                }
            </Container> */}
         
        </Box>
    )
}
