import react from 'react';
import './App.scss';
import {Container,Box,Grid,Button} from '@mui/material/';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';


import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import {CardActionArea, CardActions } from '@mui/material';

import covidImg1 from "./projects-image/covidImg1.png";
import portfolioImgv1 from "./projects-image/portfolioImgv1.png";
import abapsourcecode from "./projects-image/abapsourcecode.PNG";

import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();


const githublinks = [
    {
        name:"ABAP(REUSE_ALV_GRID_DISPLAY)",
        href:"https://github.com/AaronBolival/completeAbap/blob/3402d157af6d6f8a270d0bc7f050f28b9a0e9286/src/zfm_alvgrid_abolival.prog.abap",
        desc:"Simple ALV Report using REUSE_ALV_GRID_DISPLAY function module",
        category:"ABAP Program, Function Module"
    },
    {
        name:"ABAP(SPELL_AMOUNT)",
        href:"https://github.com/AaronBolival/completeAbap/blob/3402d157af6d6f8a270d0bc7f050f28b9a0e9286/src/zfm_spellamount_abolival.prog.abap",
        desc:"Spell amount function module",
        category:"ABAP Program, Function Module"
    },
    {
        name:"ABAP(TEXT_CONVERT_XLS_TO_SAP)",
        href:"https://github.com/AaronBolival/completeAbap/blob/3402d157af6d6f8a270d0bc7f050f28b9a0e9286/src/zfm_txtconvxls_abolival.prog.abap",
        desc:"Simple Proram to convert xls to sap function module",
        category:"ABAP Program, Function Module"
    },
    {
        name:"ABAP(FILE_OPEN_DIALOG)",
        href:"https://github.com/AaronBolival/completeAbap/blob/3402d157af6d6f8a270d0bc7f050f28b9a0e9286/src/zcl_fileodialog_abolival.prog.abap",
        desc:"Simple File open dialog function module",
        category:"ABAP Program, Function Module"
    },
    {
        name:"ABAP(CALL_BROWSER)",
        href:"https://github.com/AaronBolival/completeAbap/blob/3402d157af6d6f8a270d0bc7f050f28b9a0e9286/src/zfm_callbrowser_abolival.prog.abap",
        desc:"calling the link",
        category:"ABAP Program, Function Module"
    }
  
]
export default function Programs(){
    return(
        <Box className="projects-con" id="programs">

            
            <Typography variant="h2" align="center">Sample Programs</Typography>
            <Divider/>
            <Typography variant="h5" align="center" className="typography-programs-title" color="secondary">ABAP</Typography>
            <Container className="projects-con-list2">
            
                {
                    githublinks.map(githublink=>{
                        return(
                            <Card sx={{ maxWidth: 385 }} className="projects-card"  data-aos="zoom-in">
                               
                                <CardContent>
                                    <Typography gutterbottom variant="h6" component="div" color="darkslateblue" className="program-title">
                                        {githublink.name}
                                    </Typography>
                                    <Typography variant="body2">
                                        {githublink.desc}
                                    </Typography>
                                </CardContent>
                                <Divider/>
                                <CardContent>
                                    <Typography variant="body2">
                                        Category: {githublink.category}
                                    </Typography>
                                </CardContent>
                                <Divider/>
                                <CardActions>
                                    <Button size="small" href={githublink.href} target="_blank">Github Link</Button>
                                </CardActions>
                            </Card>
                        )
                    })
                }
            </Container>
         
        </Box>
    )
}
